
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.jumbotron p{
  
  font-weight: 600;
}

.left-half2{
  flex:1;
}
.left-half{
  margin: auto;
  flex:2;
  vertical-align: middle;
}
.right-half2{
  flex:1;
}
.right-half{
  margin: auto;
  flex:1;
}
.myimg{ 
  width:100px;
  height:100px;
  object-fit:cover;
  border-radius:50%;
}
.projimg{ 
  height:500px;
  object-fit:cover;
  
}
.myStyle{
  text-align:center;
  background-color : rgba(255, 255, 255, 0.3);
  -webkit-backdrop-filter: blur(50px);
  backdrop-filter:blur(50px);
}

.myStyle2{
  background-color : rgba(255, 255, 255, 0.3);
  -webkit-backdrop-filter: blur(50px);
  backdrop-filter:blur(50px);
}

.card{
  margin: 0 auto;  
  -webkit-box-shadow: 10px 10px 19px -14px rgba(0,0,0,0.75);
-moz-box-shadow: 10px 10px 19px -14px rgba(0,0,0,0.75);
box-shadow: 10px 10px 19px -14px rgba(0,0,0,0.75);
}



.navbar,
.navbar-default {
  background-color: rgba(255, 255, 255, 0.3);  
  backdrop-filter: blur(50px);
}
